<template>
  <div class="myBankcards grayBody">
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Bank Cards</h1>
      <div class="chartWrap">
        <van-icon name="add-o" @click="$router.push('addBankcard')" />
      </div>
    </section>
    <ul>
      <li v-for="(item, index) in list" :key="index">
        <van-cell-group>
          <van-cell
            :title="item.cardType == 1 ? 'Credit Card' : 'Debit Card'"
          />
        </van-cell-group>
        <div class="cardsWrap">
          <div class="cards_left">
            <img
              style="width: 22px; height: 16px;"
              src="../common/images/bankCrards.png"
            />
          </div>
          <div class="cards_center">
            <p>{{ item.name }}</p>
            <span>{{ item.cardNum }}</span>
          </div>
          <div class="cards_right">
            <a
              href="javascript:void(0)"
              @click="changePrimary(item.id,item)"
              class="iconfont stars"
              :style="{'color': item.defaultCard==1?'#FDBA05':'#999999','margin-right': '10px'}"
            >
              &#xe65f;
            </a>
            <a class="iconfont delete" @click="delCard(item.id)"> &#xe609; </a>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { myBankCards, deleteCard,defaultCard } from "@/api/bankCard";
export default {
  name: "myBankcards",
  data() {
    return {
      list: []
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      myBankCards().then(res => {
        this.list = res.data.result;
      });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    changePrimary(id,item) {
      if(item.defaultCard==1){
        return false;
      }
         this.$dialog
        .confirm({
          message: "Confirm to Change?",
          confirmButtonText: "confirm",
          cancelButtonText: "Cancel"
        })
        .then(() => {
          // on confirm
          defaultCard({id}).then(res=>{
            this.$toast('Set as default successfully');
             this.init();
          })
        })
        .catch(() => {
          // on cancel
        });
    },
    delCard(id) {
      this.$dialog
        .confirm({
          message: "Confirm to Delete?",
          confirmButtonText: "confirm",
          cancelButtonText: "Cancel"
        })
        .then(() => {
          // on confirm
          deleteCard({id:id}).then(res=>{
              this.$toast('Delete Successfully');
              this.init();
          })
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>

<style lang="scss" scoped>

    .head{
        padding: 0 10px 0 10px;
    }
    .myBankcards{
        ul{
            padding-top: 10px;
            background:rgba(246,246,246,1);
        }
        li{
            background-color: #fff;
            margin-bottom: 10px;
        }
        .van-cell{
        font-size: 15px;
        color: #333333;
        }
    }

    .cardsWrap{
        padding: 0 15px;
        height: 60px;
        display: -webkit-box;
        .delete{color:#999;}
    }
    .cards_left{
        height: 100%;
        width: 10%;
    }
    .cards_left img{
        margin-top: 15px;
    }
    .cards_center{
        width: 60%;
    }
    .cards_right{
        height: 100%;
        width: 30%;
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        padding: 0 10px 10px 0;
    }
    .cards_center p{
        padding: 10px 0 5px 0;
        font-size: 15px;
        color: #111111;
    }
    .cards_center span{
        font-size: 14px;
        color: #666666;
    }
  .chartWrap{
       font-size: 21px;
       color: #e33946;
       right: 0;
  }
</style>



